
import React from 'react';
import {  Link } from 'react-router-dom';
import { /*getCustomer, */postCustomer } from '../../repository';
var faker = require('faker');
const random = require("lodash");
const LOCALSTORAGE_KEY = "customer";


export default class Customer extends React.Component{
    constructor(props) {
    super(props);
    this.handleChange = this.handleChange.bind(this);
    this.handleSubmit = this.handleSubmit.bind(this);
    let id = faker.fake("{{random.uuid}}")
    let buildingName = 'Exchange House'
    let flatNumber = faker.fake("{{address.secondaryAddress}}") //this.state.flatNumber,
    let houseNumber = random.random(999) //this.state.houseNumber,
    let line2 = faker.fake("{{address.streetAddress}}") //this.state.streetName,
    let line4 = faker.fake("{{address.streetName}}") //this.state.streetAddress,
    let town = faker.fake("{{address.city}}") //this.state.city
    let county = faker.fake("{{address.county}}") //this.state.county,
    let postCode = faker.fake("{{address.zipCode}}") //this.state.zipCode,
    let email = faker.fake("{{internet.email}}") //this.state.email,
    let phone = faker.fake("{{phone.phoneNumber}}") //this.state.phoneNumber,
    let dateOfBirth = faker.fake("{{date.past}}") //this.state.dateOfBirth,
    let dependents = random.random(9) //..this.state.dependents,
    let employmentStatus = 'EMPLOYED' //this.state.employmentStatus,
    let accountNumber = faker.fake("{{finance.account}}") //this.state.accountNumber,
    let sortCode = '20-20-15' //this.state.sortCode
    let grossAnnualIncome = faker.fake("{{finance.amount}}") //this.state.currencySymbol + this.state.grossAnnualIncome
    let firstName = faker.fake("{{name.firstName}}") //this.state.firstName,
    let lastName = faker.fake("{{name.lastName}}") //this.state.lastName,
    let maritalStatus = 'MARRIED'
    let middleName = 'G'
    let residentialStatus = 'HOME_OWNER'
    let title = 'Mr' //this.state.title

    this.state = ({
      id: id,
      buildingName: buildingName,
      flatNumber: flatNumber,
      houseNumber: houseNumber,
      line2: line2,
      line4: line4,
      town: town,
      county: county,
      postCode: postCode,
      email: email,
      phone: phone,
      dateOfBirth: dateOfBirth,
      dependents: dependents,
      employmentStatus: employmentStatus,
      accountNumber: accountNumber,
      sortCode: sortCode,
      grossAnnualIncome: grossAnnualIncome,
      firstName: firstName,
      lastName: lastName,
      maritalStatus: maritalStatus,
      middleName: middleName,
      residentialStatus: residentialStatus,
      title: title
    })
  }

    componentDidMount() {
      window.localStorage.setItem(LOCALSTORAGE_KEY, JSON.stringify(this.state));
      console.log(this.state)
    }

    handleChange(event) {
      this.setState({value: event.target.value});
    }
  
    handleSubmit(event) {
      event.preventDefault();
    }

    addCustomer(event) {
      event.preventDefault();
      let payload = localStorage.getItem(LOCALSTORAGE_KEY);
      postCustomer(payload)
      .then(customer => window.location = '/')
      .catch(err => alert(err));
    }

    render() {
      return (
        <div className="container">
        <hr/>
          <div className="col-sm-8 col-sm-offset-2">
            <div className="panel panel-primary">
              <div className="panel-heading">
                <h3>Customer</h3>
              </div>
              <div className="panel-body">
                <form onSubmit={this.addCustomer}>
                  <div className="form-group">
                    <label>Title:</label>
                    <input type="text" className="form-control" name="title" value={this.state.title} onChange={this.handleFormChange}/>
                  </div>
                  <div className="form-group">
                    <label>First Name:</label>
                    <input type="text" className="form-control" name="firstName" value={this.state.firstName} onChange={this.handleFormChange}/>
                  </div>
                  <div className="form-group">
                    <label>Last Name:</label>
                    <input type="text" className="form-control" name="lastName" value={this.state.lastName} onChange={this.handleFormChange}/>
                  </div>
                  <div className="form-group">
                    <label>Building Name:</label>
                    <input type="text" className="form-control" name="buildingName" value={this.state.buildingName} onChange={this.handleFormChange}/>
                  </div>
                  <div className="form-group">
                    <label>Flat Number:</label>
                    <input type="text" className="form-control" name="flatNumber" value={this.state.flatNumber} onChange={this.handleFormChange}/>
                  </div>
                  <div className="form-group">
                    <label>House Number:</label>
                    <input type="text" className="form-control" name="houseNumber" value={this.state.houseNumber} onChange={this.handleFormChange}/>
                  </div>
                  <div className="form-group">
                    <label>Address Line 2:</label>
                    <input type="text" className="form-control" name="line2" value={this.state.line2} onChange={this.handleFormChange}/>
                  </div>
                  <div className="form-group">
                    <label>Address Line 4:</label>
                    <input type="text" className="form-control" name="line4" value={this.state.line4} onChange={this.handleFormChange}/>
                  </div>
                  <div className="form-group">
                    <label>Town:</label>
                    <input type="text" className="form-control" name="town" value={this.state.town}  onChange={this.handleFormChange}/>
                  </div>
                  <div className="form-group">
                    <label>Post Code:</label>
                    <input type="text" className="form-control" name="postCode" value={this.state.postCode} onChange={this.handleFormChange}/>
                  </div>
                  <div className="form-group">
                    <label>County:</label>
                    <input type="text" className="form-control" name="County" value={this.state.county}  onChange={this.handleFormChange}/>
                  </div>
                  <div className="form-group">
                    <label>Email:</label>
                    <input type="text" className="form-control" name="email" value={this.state.email}  onChange={this.handleFormChange}/>
                  </div>
                  <div className="form-group">
                    <label>Phone Number:</label>
                    <input type="text" className="form-control" name="phoneNumber" value={this.state.phone}  onChange={this.handleFormChange}/>
                  </div>
                  <div className="form-group">
                    <label>Date Of Birth:</label>
                    <input type="text" className="form-control" name="dateOfBirth" value={this.state.dateOfBirth}  onChange={this.handleFormChange}/>
                  </div>
                  <div className="form-group">
                    <label>Dependents:</label>
                    <input type="text" className="form-control" name="dependents" value={this.state.dependents}  onChange={this.handleFormChange}/>
                  </div>
                  <div className="form-group">
                    <label>Employment Status:</label>
                    <input type="text" className="form-control" name="employmentStatus" value={this.state.employmentStatus}  onChange={this.handleFormChange}/>
                  </div>
                 <div className="form-group">
                    <label>Sort Code:</label>
                    <input type="text" className="form-control" name="sortCode" value={this.state.sortCode}  onChange={this.handleFormChange}/>
                  </div>
                  <div className="form-group">
                    <label>Account Number:</label>
                    <input type="text" className="form-control" name="accountNumber" value={this.state.accountNumber}  onChange={this.handleFormChange}/>
                  </div>
                  <div className="form-group">
                    <label>Gross Annual Income:</label>
                    <input type="text" className="form-control" name="grossAnnualIncome" value={this.state.grossAnnualIncome}  onChange={this.handleFormChange}/>
                  </div>

                  
                  <button type="submit" className="btn btn-success float-right" onClick={this.addCustomer} style={{ marginRight: "10px" }}>Save</button>
                  <Link to="/"><button className="btn btn-danger float-right" style={{ marginRight: "10px" }}>Cancel</button></Link>
                  <br/><br/><br/>
                </form>
              </div>
            </div>
          </div>
        </div>
      );
    }
}